import React, { useState } from 'react';

function UserBets({ user }) {

    let keyBets = Object.keys(user.bets);

    const orderBets = []
    keyBets.map((id) => {
        orderBets[id] = {
            date: user.bets[id].date || 0,
            id: id
        }
    });
    orderBets.sort((a, b) => {
        return b.date - a.date;
    })

    let listBets = orderBets.map((value) => {
        const key = value.id;
        const currentBet = user.bets[key];
        if (currentBet.valid) {
            const StyleTeamA = {
                backgroundImage: `url("../imgs/teams/${currentBet.TeamA}.png")`
            };
            const StyleTeamB = {
                backgroundImage: `url("../imgs/teams/${currentBet.TeamB}.png")`,
            };
            const StyleScore = {
                border: (currentBet.score > 0) ? (currentBet.score > 20) ? '2px solid rgb(26, 209, 255)' : '2px solid rgb(0, 153, 51)' : '2px solid rgb(204, 51, 0)',
                backgroundColor: (currentBet.score > 0) ? (currentBet.score > 20) ? 'rgba(230, 250, 255)' : 'rgba(230, 255, 230, 1)' : 'rgba(255, 230, 230, 1)',
            }
            return (
                <div className="bet flex three" key={key}>

                    <div className="TeamA" style={StyleTeamA}><img src={`../imgs/teams/${currentBet.TeamA}.png`} alt="" /></div>
                    <div>R : </div>
                    <div>{currentBet.resultA}</div>
                    <div>{currentBet.resultB}</div>
                    <div>P :</div>
                    <div>{currentBet.scoreA}</div>
                    <div>{currentBet.scoreB}</div>
                    {/* <div>{currentBet.type}</div> */}
                    <div className="TeamB" style={StyleTeamB}><img src={`../imgs/teams/${currentBet.TeamB}.png`} alt="" /></div>
                    <div className="score" style={StyleScore}>{currentBet.score}</div>
                </div>
            )
        }


    })

    return (
        <div className="UserBets">
            <div className="flex center six">
                {listBets}
            </div>
            <div className="legend flex center six">
            <div style={{
                border: '2px solid rgb(26, 209, 255)',
                backgroundColor: 'rgba(230, 250, 255)',
                borderRadius: '50%',
                width:'50px',
                height:'50px',
                lineHeight: '50px',
                textAlign: 'center',
                padding: 0,
                marginLeft: '10px',
            }}>FULL</div>
            <div style={{
                border: '2px solid rgb(0, 153, 51)',
                backgroundColor: 'rgba(230, 255, 230, 1)',
                borderRadius: '50%',
                width:'50px',
                height:'50px',
                lineHeight: '50px',
                textAlign: 'center',
                padding: 0,
                marginLeft: '10px',
            }}>win</div>
            <div style={{
                border: '2px solid rgb(204, 51, 0)',
                backgroundColor: 'rgba(255, 230, 230, 1)',
                borderRadius: '50%',
                width:'50px',
                height:'50px',
                lineHeight: '50px',
                textAlign: 'center',
                padding: 0,
                marginLeft: '10px',
            }}>lose</div>
            </div>
        </div>
    )

}

export default UserBets;