import React, { useState, useEffect } from 'react';
import { Redirect, withRouter } from 'react-router-dom';

const axios = require('axios');

function Match({ match, history }) {
    const [game, setGame] = useState(null);
    const [idMatch] = useState(match.params.idMatch);

    if (game === null) {
        axios.get(`https://api.overwatchleague.com/match/${idMatch}`)
            .then(function (response) {
                setGame(response.data);
            })
            .catch(function (error) {
                console.log(error);
            });
        return (
            <div>
                <h2>MATCH</h2>
            </div>
        );
    } else {
        let Maps = [];
        const PlayerName = []
        console.log(game.competitors[0].players);
        game.competitors[0].players.map(({ player }) => {
            PlayerName[player.id] = player.name
        })
        game.competitors[1].players.map(({ player }) => {
            PlayerName[player.id] = player.name
        })
        for (let index = 0; index < game.games.length; index++) {
            Maps.push(<Map key={index} idMatch={idMatch} game={game} map={game.games[index]} PlayerName={PlayerName}  ></Map>)
        }
        return (
            <div className="Game">
                <h2>MATCH</h2>
                <div className="flex two">
                    <div>{game.competitors[0].abbreviatedName}</div>
                    <div>{game.competitors[1].abbreviatedName}</div>
                </div>
                {Maps}
            </div>
        )
    }
}

function Map({ map, idMatch, PlayerName, game }) {
    const nameMap = map.attributes.map || '';
    const number = map.number
    const [stat, setStat] = useState(null);

    if (stat === null) {
        axios.get(`https://api.overwatchleague.com/stats/matches/${idMatch}/maps/${number}`)
            .then(function (response) {
                setStat(response.data);
            })
            .catch(function (error) {
                console.log(error);
            });
        return (
            <div>
                Map
        </div>
        )
    } else {

        return (
            <div>
                {nameMap}
                <div className="Stat flex two">
                    <div className="TeamA">
                        {map.points[0]}
                        <StatMap stat={stat.teams[0]} PlayerName={PlayerName}></StatMap>
                    </div>
                    <div className="TeamB">
                        {map.points[1]}
                        <StatMap stat={stat.teams[1]} PlayerName={PlayerName}></StatMap>
                    </div>
                </div>
            </div>
        )
    }


}

function StatMap({ stat, PlayerName }) {
    let Players = [];
    Players.push(<div className="Players flex" key={0}>
        <div className="name">Pseudo</div>
        <div className="Heroes">Hero</div>
        <div className="dmg">DMG</div>
        <div className="death">X</div>
        <div className="kill">KILL</div>
        <div className="heal">HEAL</div>
    </div>)
    stat.players.map((player, key) => {
        let Heroes = [];
        player.heroes.map((heroe) => {
            Heroes.push(<div className={`ohi ${heroe.name}`} key={heroe.name}></div>)
        })
        Players.push(<div className="Players flex" key={player.esports_player_id}>
            <div className="name">{PlayerName[player.esports_player_id]} </div>
            <div className="Heroes">
                {Heroes}
            </div>
            <div className="dmg">{(player.stats[0]) ? Math.floor(player.stats[0].value) : ''}</div>
            <div className="death">{(player.stats[1]) ? Math.floor(player.stats[1].value) : ''}</div>
            <div className="kill">{(player.stats[2]) ? Math.floor(player.stats[2].value) : ''}</div>
            <div className="heal">{(player.stats[3]) ? Math.floor(player.stats[3].value) : ''}</div>
        </div>)
    })


    return (<div className="StatMap">
        {Players}
    </div>)
}

export default withRouter(Match);